import { Injectable } from '@angular/core';
import { Contact } from '../contact';
//import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { HttpHeaders, HttpClient } from '@angular/common/http';
//import { AuthenticationService } from './authentication.service';
import { tap } from 'rxjs/operators';
//import { Observable } from 'rxjs/Observable';
import { environment } from '../../environments/environment';
import { BehaviorSubject } from 'rxjs';

@Injectable()
export class ContactService {

  contacts: Array<Contact> = [];
  contactById: Contact;


  contactsSubject = new BehaviorSubject([]);
  contactDetailSubject = new BehaviorSubject(Contact.prototype);

  //notesSubject = new BehaviorSubject([]);
  // editnote: Note = new Note();
  //noteurl: string = environment.noteurl;
  //constructor(private http: HttpClient, private authService: AuthenticationService) {
  constructor(private http: HttpClient) {
    // this.fetchNotesFromServer();
    
  }

  getAllContactsOld(): BehaviorSubject<Array<Contact>> {
    this.contacts = [{name: "Tom", email: "tom@gmail.com",address:"#1, Tom Street, CA", phone: "9999900000", id:1},
    {name: "Harry", email: "harry@gmail.com",address:"#2, Harry Street, CA", phone: "9999911111", id:2}
  ];
    this.contactsSubject.next(this.contacts);
    return this.contactsSubject;
  }

  getAllContacts() {
    this.http.get<Array<Contact>>("https://localhost:44300/GetAllContacts").subscribe(
      data => {
        if(data != null){
        if (data.length > 0) {
          this.contacts = data;
          this.contactsSubject.next(this.contacts);
        } else {
          this.contacts = [];
          this.contactsSubject.next(this.contacts);
        }
      }
        else {
          this.contacts = [];
          this.contactsSubject.next(this.contacts);
        }
      },
      error => {
      }
    );
    return this.contactsSubject;
  }
  getContactById(id) {

    this.http.get<Contact>("https://localhost:44300/GetContactDetailsById/" + id).subscribe(
      data => {
        if(data != null){
          this.contactById = data;
          this.contactDetailSubject.next(this.contactById);
      }
        else {
          this.contactById = null;
          this.contactDetailSubject.next(this.contactById);
        }
      },
      error => {
      }
    );
return this.contactDetailSubject;

    //this.contact = {name: "Tom", email: "tom@gmail.com",address:"#1, Tom Street, CA", phone: "9999900000", id:1};
    //this.contactSubject.next(this.contact);
    //return Object.assign({}, this.contact);
  }
}
