import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Contact } from '../contact';
import { ContactService } from '../services/contacts.service';
import {RouterService} from '../services/router.service'

@Component({
  selector: 'app-contacts-list',
  templateUrl: './contacts-list.component.html',
  styleUrls: ['./contacts-list.component.css']
})
export class ContactsListComponent implements OnInit {

  contacts: Array<Contact> = [];
  name: string;
  constructor(private contactService: ContactService, private routerService: RouterService) { 
    this.fetchContactsFromServer();
  }

  ngOnInit(): void {
  }

  contactClick(clickedId){
    this.routerService.routeToContactDetails(clickedId);
  }
fetchContactsFromServer() {
  const obs = this.contactService.getAllContacts();

  obs.subscribe(data => {
    this.contacts = data;
  },
  error => {
    // console.log(error);
  });
}
}

