import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router'
import { Contact } from '../contact';
import { ContactService } from '../services/contacts.service';
import { RouterService } from '../services/router.service';

@Component({
  selector: 'app-contact-details',
  templateUrl: './contact-details.component.html',
  styleUrls: ['./contact-details.component.css']
})
export class ContactDetailsComponent implements OnInit {
  id: number;
  contacts: Array<Contact> = [];
  contact: Contact;
  constructor(private route: ActivatedRoute, private contactService: ContactService, private routerService: RouterService) { }

  ngOnInit(): void {
    this.id = Number(this.route.snapshot.paramMap.get('id'));
    this.getContactDetails();
  }

getContactDetails(){

  const obs = this.contactService.getContactById(this.id);

  obs.subscribe(data => {
    this.contact = data;
  },
  error => {
    // console.log(error);
  });


}
goBack(){
  this.routerService.routeToContacts();
}
}
